.App{
    margin:0;
    padding-top: 18vw;
    text-align: center;
}

.title{
    margin: 1.5vw;
    
}
.text{
    font-size: larger;
}

.upload_btn, .download_btn{
    background-color: #326e86;
    transition: 0.3s;
    color: #FEF9EF;
    border: none;
    padding: 15px 35px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    margin: 1vw;
    cursor: pointer;
    border-radius: 20px;
}

.upload_btn:hover:enabled, .download_btn:hover:enabled{
    background-color: #102d38;
}

.download_btn:disabled, .upload_btn:disabled{
    background-color: gray;
    pointer-events: none;
}
